<template>
	<view class="home_tab">
		<view class="home_tab_title">
			<view class="title_inner">
				<uni-segmented-control :current="current" :values="items.map(it=>it.title)"
				@clickItem="onClickItem" style-type="text" active-color="#d4237a"></uni-segmented-control>
				
				<view class="iconfont iconsearch"></view>
			</view>
		</view>
		<view class="home_content">
			<view v-if="current === 0">
				<home-recommend></home-recommend>
			</view>
			<view v-if="current === 1">
				<home-category></home-category>
			</view>
			<view v-if="current === 2">
				<home-new></home-new>
			</view>
			<view v-if="current === 3">
				<home-album></home-album>
			</view>
		</view>
	</view>
</template>

<script>
	import uniSegmentedControl from "@/components/uni-segmented-control/uni-segmented-control.vue"
	import homeAlbum from './home-album/home-album.vue'
	import homeCategory from './home-category/home-category.vue'
	import homeNew from './home-new/home-new.vue'
	import homeRecommend from './home-recommend/home-recommend.vue'
	export default {
		components: {
			homeAlbum,
			homeCategory,
			homeNew,
			homeRecommend,
			uniSegmentedControl
		},
		data() {
			return {
				items: [
					{title:'推荐'},
					{title:'分类'},
					{title:'最新'},
					{title:'专辑'}
				],
				current: 1
			}
		},
		onLoad() {
			
		},
		methods: {
			onClickItem(e) {
				if (this.current !== e.currentIndex) {
					this.current = e.currentIndex;
				}
			}
		}
	}
</script>

<style lang="scss">
	.home_tab{
		.home_tab_title{
			position: relative;
			.title_inner{
				width: 60%;
				margin: 0 auto;
				.iconsearch{
					position: absolute;
					top: 50%;
					transform: translateY(-50%);
					right: 8%;
				}
			}
		}
		.home_content{}
	}
</style>
